import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';

const initialState = {
    orders: [],
    loading: false,
    purchased: false

}

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.PURCHASE_INIT: return updateObject(state, { purchased: false })
        case actionTypes.PURCHASE_BURGER_SUCCESS: return purchaseBurgetSuccess(state, action);
        case actionTypes.PURCHASE_BURGER_START:
        case actionTypes.FETCH_ORDERS_START: return updateObject(state, { loading: true })
        case actionTypes.FETCH_ORDERS_SUCCESS: return updateObject(state, { orders: action.orders, loading: false })
        case actionTypes.PURCHASE_BURGER_FAIL:
        case actionTypes.FETCH_ORDERS_FAIL: return updateObject(state, { loading: false })
        default: return state;
    }
}

const purchaseBurgetSuccess = (state, action) => {
    const newOrder = {
        ...action.orderData,
        id: action.orderId,
    }
    return updateObject(state, { loading: false, purchased: true, orders: state.orders.concat(newOrder) });
}


export default reducer;